:root {
  --accent: 124, 58, 237;
  --accent-gradient: linear-gradient(
    45deg,
    rgb(var(--accent)),
    #da62c4 30%,
    white 60%
  );
}
html {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica,
    sans-serif;
  background-color: #f6f6f6;
}
body {
  margin: 0 !important;
}
main {
  margin: auto;
  padding: 1.5rem;
  --main-color: #3973e0;
  min-height: 100vh;
  @apply flex flex-col items-center justify-center;
}

.card {
  max-width: 800px;
  @apply flex flex-col items-center;
  @apply bg-white pt-6 pb-8 px-12 mx-auto;
  @apply rounded-lg shadow-lg;
}
.title {
  @apply text-2xl font-normal mb-4 text-gray-600;
}
footer {
  @apply flex flex-row w-full justify-between mt-6;
}
.right-area {
  @apply flex flex-col;
}
.right-area a {
  @apply whitespace-nowrap;
}
@media (max-width: 768px) {
  .card {
    @apply pt-3 pb-4 px-2 w-full;
  }
  body {
    @apply w-full;
  }
  main {
    @apply p-2 min-h-screen justify-start;
    background-color: white;
  }
}
.ghost {
  @apply py-2 px-5 rounded-lg border-2 h-fit;
  color: var(--main-color);
  border-color: var(--main-color);
}
.disabled {
  @apply py-2 px-5 rounded-lg border-2 h-fit;
  color: #ccc;
  border-color: #ccc;
}
.primary {
  @apply py-2 px-5 rounded-lg;
  color: white;
  background-color: var(--main-color);
}
.secondary {
  @apply text-gray-500 mt-4 text-center text-sm;
}
